* {
  box-sizing: border-box; }

html {
  height: 100%; }

body {
  font-family: "Roboto", "Arial", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  margin: 0;
  width: 100%;
  height: 100%;
  min-width: 320px;
  min-height: 100vh;
  line-height: 1; }

.header,
.footer {
  flex-shrink: 0; }

main {
  flex-grow: 1; }

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

button,
input {
  -webkit-appearance: none; }

textarea {
  -webkit-appearance: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.scroll-lock {
  min-height: 100vh;
  overflow: hidden; }

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto; }

.input {
  font-size: 14px;
  width: 250px;
  height: 60px;
  border-radius: 57px;
  border: 2px solid #ECEEF7;
  color: #000000;
  transition: opacity 0.2s ease;
  outline: none; }
  .input:hover {
    opacity: 0.7; }
  .input::placeholder {
    color: #222222; }
  .input.invalid {
    color: red;
    border: 1px solid red; }
    .input.invalid:focus {
      border-color: red; }

.btn {
  border: none;
  outline: none;
  background-color: transparent;
  padding: 0;
  transition: opacity 0.2s ease;
  cursor: pointer; }
  .btn:hover {
    opacity: 0.7; }

.section {
  display: flex;
  flex-direction: column;
  align-items: center; }

.section-title {
  font-size: 46px;
  line-height: 57px;
  letter-spacing: 0.2px;
  font-weight: normal;
  margin: 0 0 27px;
  max-width: 296px;
  color: #252b42;
  text-align: center; }
  .section-title--mobile {
    display: none; }
    @media (max-width: 767px) {
      .section-title--mobile {
        display: block;
        margin-bottom: 37px;
        margin-top: 10px; } }
  @media (max-width: 767px) {
    .section-title--desktop {
      display: none; } }

.section-subtitle {
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.2px;
  font-weight: bold;
  max-width: 298px;
  margin: 0;
  color: #374754;
  text-align: center; }

.section-entry {
  font-size: 22px;
  font-weight: bold; }

.footer {
  display: flex;
  padding: 130px 214px 120px 204px; }
  @media (max-width: 1439px) {
    .footer {
      padding: 100px 50px 100px;
      max-width: 1206px;
      flex-wrap: wrap; } }
  @media (max-width: 767px) {
    .footer {
      flex-direction: column;
      padding: 141px 30px 106px; } }
  .footer__map {
    width: 472px;
    height: 247px;
    margin-right: 45px;
    border-radius: 22px; }
    @media (max-width: 1439px) {
      .footer__map {
        order: 1;
        flex-grow: 1;
        width: 100%;
        margin-right: 0;
        margin-bottom: 40px; } }
    @media (max-width: 767px) {
      .footer__map {
        order: 2; } }
  .footer .contacts-links__list {
    color: #374754;
    margin: 0 0 55px; }
  .footer__list {
    margin: 0;
    padding: 0;
    list-style: none; }
    @media (max-width: 767px) {
      .footer__list {
        display: flex;
        flex-direction: column;
        align-items: center; } }
  .footer__title {
